import React from 'react'
import { Route, Link } from 'react-router-dom'
import { Avatar, Icon, Button } from 'antd';
import './index.less'

export default class About extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            avatarUrl: '',
            nickName: '',
            token: '',
        }
    }
    componentDidMount() {
        // 获取用户数据
        // if (localStorage.hadLoad === undefined || localStorage.hadLoad === false) {
        //     console.log('LOading');
        if (document.cookie === undefined) {
            // 新用户链接
            console.log('new');
            // localStorage.hadLoad = true
            document.location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx5e44595fadf1d2a2&redirect_uri=https%3a%2f%2fwww.newbrandser.com%2fwechat_redirect&response_type=code&scope=snsapi_userinfo&state=index.html%23%2fabout'
        } else {
            console.log('old');
            // 老用户
            // localStorage.hadLoad = true
            document.location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx5e44595fadf1d2a2&redirect_uri=https%3a%2f%2fwww.newbrandser.com%2fwechat_redirect&response_type=code&scope=snsapi_base&state=index.html%23%2fabout'
            // 设置用户信息
        }
        // }
        if (document.cookie !== undefined) {
            // 设置信息
            this.setState({
                avatarUrl: unescape(document.cookie.split(";")[0].split("=")[1]),
                nickName: document.cookie.split(";")[1].split("=")[1],
                token: document.cookie.split(";")[3].split("=")[1],
            })
        }
    }
    render() {
        return (
            <div className="About">
                {/* 用户名与头像 */}
                <div className='header'>
                    <Avatar size={64} src={this.state.avatarUrl} />
                    <span className='user-name'>{this.state.nickName}</span>
                </div>
                {/* 发布任务 */}
                <div className='user-actions'>
                    <Link to={`${this.props.match.url}/post`}>
                        <div className='post-new'>

                            <Icon type="plus-circle" style={{ fontSize: '30px' }} />
                            <div >
                                发布新任务
                                </div>
                        </div>
                    </Link>
                </div>
                <hr></hr>
                {/* 子路由导航 */}
                <div className='nav-route' >
                    <Link
                        to={`${this.props.match.url}/MyFav`}
                        className={document.location.hash.includes('MyFav') ? 'active' : 'unactive'}
                    >
                        <Icon type="star" style={{ fontSize: '30px' }} />
                        <div>
                            我的收藏
                        </div>

                    </Link>
                    <Link
                        to={`${this.props.match.url}/TaskPosted`}
                        className={document.location.hash.includes('TaskPosted') ? 'active' : 'unactive'}
                    >
                        <Icon type="carry-out" style={{ fontSize: '30px' }} />
                        <div>
                            我发布的任务
                        </div>
                    </Link>
                    <Link
                        to={`${this.props.match.url}/TaskAcepted`}
                        className={document.location.hash.includes('TaskAcepted') ? 'active' : 'unactive'}
                    >
                        <Icon type="container" style={{ fontSize: '30px' }} />
                        <div>
                            我接受的任务
                        </div>

                    </Link>
                </div>
                {/* 子路由 */}
                <div className='routes-container'>
                    <Route exact path={this.props.match.url} render={() => (
                        <MyFav />
                    )} />
                    <Route exact path={`${this.props.match.url}/MyFav`} render={() => (
                        <MyFav />
                    )} />
                    <Route exact path={`${this.props.match.url}/TaskPosted`} render={() => (
                        <TaskPosted />
                    )} />
                    <Route exact path={`${this.props.match.url}/TaskAcepted`} render={() => (
                        <TaskAcepted />
                    )} />
                </div>
            </div >

        )
    }
}
const TaskPosted = () => {
    return (<div>我发布的任务</div>)
}
const TaskAcepted = () => {
    return (<div>我接受的任务</div>)
}
const MyFav = () => {
    return (<div>我的收藏</div>)
}